import React from 'react'
import { Modal, Button } from 'react-bootstrap';
import { ethers, providers, Contract } from "ethers";
import { useState, useEffect } from 'react'
import { useWeb3React } from "@web3-react/core";
import useSWR from 'swr';

import '../../assets/widget/MainPageStyle.css'
import { portalABI, portalContractAddress, apiURL } from '../../config/config';
import { connectors } from "../../config/connectors";

import MainHeader from './partials/MainHeader';
import MainBody from './partials/MainBody';
import MainFooter from './partials/MainFooter';


const MainPage = () => {
    const fetcher = (library) => (...args) => {
        const [method, ...params] = args
        return library[method](...params)
    }
    const { chainId, account, activate, library, deactivate } = useWeb3React();
    const { data: balance } = useSWR(["getBalance", account, "latest"], {
        fetcher: fetcher(library),
    })

    const [fullName, setFullName] = useState('');
    const [telegram, setTelegram] = useState('-');
    const [twitter, setTwitter] = useState('-');
    const [amount, setAmount] = useState(0.5);
    const [history, setHistory] = useState([]);
    const [walletModal, setWalletModal] = useState(false)

    const { ethereum } = window;
    const provider = new providers.Web3Provider(ethereum);
    const signer = provider.getSigner();
    const portalContract = new Contract(portalContractAddress, portalABI, signer);

    const connectWallet = () => {
        if (account === undefined) {
            setWalletModal(true)
        }
    }

    const disconnectWallet = () => {
        deactivate();
    }

    const validation = () => {
        if (fullName === "") {
            alert('You did not enter your full name')
            return false;
        }
        if (!ethereum) {
            alert("Make sure you have Metamask installed!")
            return false;
        }
        if (account === "") {
            alert("The wallet is not connected.");
            return false;
        }
        if (chainId !== 97) {
            alert('Make sure that you selected binance test net')
            return false;
        }
        if (parseFloat(ethers.utils.formatEther(balance)).toPrecision(6) < parseFloat(amount)) {
            alert('Your balance is not enough.')
            return false;
        }
        return true;
    }

    const getTxnHistory = async () => {
        // const allTxnInfo = await portalContract.getAllHistory()
        // var transactions = [];
        // for(var i = 0; i < allTxnInfo.length; i++) {
        //     transactions.push({
        //         'name': allTxnInfo[i].fullName,
        //         'wallet_address': allTxnInfo[i].wallet,
        //         'telegram': allTxnInfo[i].telegram !== "-" ? ("@" + allTxnInfo[i].telegram) : allTxnInfo[i].telegram,
        //         'twitter': allTxnInfo[i].twitter !== "-" ? ("@" + allTxnInfo[i].twitter) : allTxnInfo[i].twitter,
        //         'amount': ethers.utils.formatEther(allTxnInfo[i].amount),
        //         'txn': allTxnInfo[i].txn
        //       })
        // }
        // setHistory(transactions)

        // const allTxnInfo = await portalContract.getUserInfo(account)

        var transactions = [];

        const response = await fetch(apiURL, { method: 'POST' });
        const txArr = await response.json();
        for (var i = txArr.result.length - 1; i >= 0; i--) {
            const info = ethers.utils.defaultAbiCoder.decode(
                ['address', 'string', 'string', 'string', 'uint256'],
                txArr.result[i].data
            )

            for (var j = 0; j < transactions.length; j++) {
                if (transactions[j].wallet_address === info[0]) {
                    transactions[j].amount += Number(ethers.utils.formatEther(info[4]));
                    break
                }
            }
            if(j === transactions.length) {
                transactions.push({
                    'name': info[1],
                    'wallet_address': info[0],
                    'telegram': info[2] !== "-" ? ("@" + info[2]) : info[2],
                    'twitter': info[3] !== "-" ? ("@" + info[3]) : info[3],
                    'amount': Number(ethers.utils.formatEther(info[4])),
                    'txn': txArr.result[i].transactionHash
                })
            }
        }

        if (transactions.length) {
            setHistory(transactions)
        }
    }

    const submit = async () => {
        if (validation() === true) {
            // let transferTxn = await portalContract.transfer(fullName, telegram, twitter, { value: ethers.utils.parseEther('0.01') })
            // await transferTxn.wait();

            // let enterNewHash = await portalContract.putNewHistory(transferTxn.hash);
            // await enterNewHash.wait();

            let transferTxn = await portalContract.contribute(fullName, telegram, twitter, { value: ethers.utils.parseEther('0.01') })
            await transferTxn.wait();

            getTxnHistory();
        }
    }

    const changeValue = (e) => {
        switch (e.target.id) {
            case 'fullName':
                setFullName(e.target.value)
                break;
            case 'telegram':
                setTelegram(e.target.value)
                break;
            case 'twitter':
                setTwitter(e.target.value)
                break;
            case 'amount':
                setAmount(e.target.value.split('_')[1])
                break;
            default:
        }
    }

    useEffect(() => {
        if (account && chainId) {
            if (chainId === 97) {
                getTxnHistory();
            }
        }
        if (account === undefined) {
            setHistory([]);
        }
    }, [account, chainId])


    return (
        <>
            <MainHeader
                connectWallet={connectWallet}
                disconnectWallet={disconnectWallet}
                walletAddress={account}
            />
            <MainBody
                fullName={fullName}
                telegram={telegram !== '-' ? telegram : ''}
                twitter={twitter !== '-' ? twitter : ''}
                amount={"BNB_" + amount}
                submit={submit}
                changeValue={changeValue}
            />
            <MainFooter history={history} />

            <Modal className="connectWalletModal" show={walletModal} onHide={() => { setWalletModal(false) }}>
                <Modal.Header closeButton>
                    <Modal.Title>Connect a wallet</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Button
                        className='walletButton'
                        onClick={() => {
                            activate(connectors.injected);
                            setWalletModal(false);
                        }}>
                        <img src={require('../../assets/images/MM.png')} alt="Metamask.png" height="30px" width="30px" style={{ marginRight: '20px' }} />
                        Metamask
                    </Button>
                    <Button
                        className='walletButton'
                        onClick={() => {
                            activate(connectors.walletConnect);
                            setWalletModal(false);
                        }}>
                        <img src={require("../../assets/images/WC.png")} alt="WalletConnect.png" height="40px" width="40px" />
                        Wallet Connect
                    </Button>
                </Modal.Body>
            </Modal>
        </>
    );
}

export default MainPage;
